.informacoes {
    margin: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 30px;
}

.informacoes img {
    width: 150px;
    order: -1;
    height: 200px;
    border-radius: 15px;
}

@media (min-width: 650px) { .informacoes img {
    width: 300px;
    order: -1;
    height: 400px;
    }   
}