.info_pessoal {
    display: flex;
    flex-direction: column;
    gap: 10px
}

.info_pessoal span {
    color: skyblue;
}

.conteudo_links {
    display: flex;
    gap:30px;
    flex-wrap: wrap;
}

.links {
    text-decoration: none;
}

.links:hover {
    transform: scale(1.2);
}

.links span:hover {
    text-decoration: underline;
    text-decoration-color: #c0c0c0;
    color: #c0c0c0;
}

.name_strong{
    font-size: 1.6rem;
    color: #c0c0c0;
}

.email {
    display: flex;
    flex-direction: column;
}

@media (min-width: 650px) {
    .email {
        gap: 5px;
    }
}