.container_skills {
    padding-top: 50px;
    display: flex;
    flex-direction: column;
}

@media (min-width: 700px) {
    .container_skills {
        display: flex;
        flex-direction: row;
        gap: 50px;
    }
}

@media (min-width: 1000px) {
    .container_skills {
        display: flex;
        gap: 150px;
    }
}

.container {
    display: flex;
    flex-direction: column;
    margin: 30px 0;
    gap: 5px;
}

@media (min-width: 1000px) {
    .container {
        gap: 15px; 
    }
}

h3 {
    font-size: 16px;
}

@media (min-width: 550px) {
    h3 {
        font-size: 20px;
    }
}

.titulo_skills{
    list-style: none;
}

.titulo_skills h3 {
    color: skyblue;
}

.container {
    width: 300px;
}

.container_itens_skills{
    display: flex;
    flex-direction: column;
    gap: 5px;
    margin-left: 50px;
}