@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap%27);
.conteudo{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

@media (min-width: 1280px) { .conteudo {
        width: 100%;
        max-width: 1280px;
    }  
}
.informacoes {
    margin: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    grid-gap: 30px;
    gap: 30px;
}

.informacoes img {
    width: 150px;
    order: -1;
    height: 200px;
    border-radius: 15px;
}

@media (min-width: 650px) { .informacoes img {
    width: 300px;
    order: -1;
    height: 400px;
    }   
}
.info_pessoal {
    display: flex;
    flex-direction: column;
    grid-gap: 10px;
    gap: 10px
}

.info_pessoal span {
    color: skyblue;
}

.conteudo_links {
    display: flex;
    grid-gap:30px;
    gap:30px;
    flex-wrap: wrap;
}

.links {
    text-decoration: none;
}

.links:hover {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}

.links span:hover {
    text-decoration: underline;
    -webkit-text-decoration-color: #c0c0c0;
            text-decoration-color: #c0c0c0;
    color: #c0c0c0;
}

.name_strong{
    font-size: 1.6rem;
    color: #c0c0c0;
}

.email {
    display: flex;
    flex-direction: column;
}

@media (min-width: 650px) {
    .email {
        grid-gap: 5px;
        gap: 5px;
    }
}
.sobre {
    display: flex;
    flex-direction: column;
    grid-gap: 5px;
    gap: 5px;
    align-items: flex-start;
    margin:0 10px;
}

@media (min-width: 1280px) { 
    .sobre {
        width: 100%;
        max-width: 1280px;
        display: flex;
        flex-direction: column;
        grid-gap: 5px;
        gap: 5px;
        align-items: flex-start;
        justify-content: center;
    }
}

.sobre_titulo {
    margin-left: 30px;
    font-size: 20px;
    color: #c0c0c0;
}

@media (min-width: 650px) { 
    .sobre_titulo {
        margin-left: 80px;
        font-size: 30px;
    }
}

.sobre_eu {
    text-indent: 15px;
}


.container_skills {
    padding-top: 50px;
    display: flex;
    flex-direction: column;
}

@media (min-width: 700px) {
    .container_skills {
        display: flex;
        flex-direction: row;
        grid-gap: 50px;
        gap: 50px;
    }
}

@media (min-width: 1000px) {
    .container_skills {
        display: flex;
        grid-gap: 150px;
        gap: 150px;
    }
}

.container {
    display: flex;
    flex-direction: column;
    margin: 30px 0;
    grid-gap: 5px;
    gap: 5px;
}

@media (min-width: 1000px) {
    .container {
        grid-gap: 15px;
        gap: 15px; 
    }
}

h3 {
    font-size: 16px;
}

@media (min-width: 550px) {
    h3 {
        font-size: 20px;
    }
}

.titulo_skills{
    list-style: none;
}

.titulo_skills h3 {
    color: skyblue;
}

.container {
    width: 300px;
}

.container_itens_skills{
    display: flex;
    flex-direction: column;
    grid-gap: 5px;
    gap: 5px;
    margin-left: 50px;
}
.rodape {
    padding-top: 100px;
    position: relative;
    bottom: 10px;   
}

@media (min-width: 1280px) { 
    .rodape {
        width: 1280px;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        left: 5%;
    }
}

header {
    width: 100%;
    height: 50px;
    background: linear-gradient(to right, #14213D, #000000);

    padding: 15px 0;

    display: flex;
    justify-content: flex-start;
    align-items: center;

    position: fixed;
    top: 0;    
}

@media(min-width: 800px) { 
    header {
        justify-content: center;
    }
}

.menu_principal {
    width: 100%;
    height: 50px;
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    align-items: flex-start;

    grid-gap: 20px;

    gap: 20px;
}

@media (min-width: 700px) {
    .menu_principal {
        max-width: 1280px;
        flex-direction: row;
        justify-content: center;
        align-items: center;
    }
}


#menu_operacional{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    
    height: 0px;
    overflow: hidden;
    visibility: hidden;

    background-color: rgba(0, 0, 0, 0.7);

    width: 100%;
    
    transition: 1s;

    z-index: 1000000;
}

#menu .menu_operacional_open {
    height: 195px;
    visibility: visible;
    position: fixed;
    padding: 20px 0;
    top: 70px;
    left: 0;
}

@media (min-width: 600px) {
    #menu .menu_operacional_open {
        height: 215px;
    }
}

@media(min-width: 700px) {
    #menu_operacional {
        max-width: 550px;
        flex-direction: row;
        justify-content: center;
        align-items: center;

        transition: 0s;
        
        grid-gap: 15px;
        
        gap: 15px;
       
        height: 70px;
        background-color: transparent;
        visibility: visible;
    }
}

@media(min-width: 1280px) { 
    #menu_operacional{
        max-width: 880px;
        grid-gap: 30px;
        gap: 30px;
    }
}

.operacional {
    text-decoration: none;
    color: #c0c0c0;
    margin: 20px 0;
    border-bottom: solid 2px #c0c0c0;
    text-indent: 20px;
}

@media (min-width: 700px) {
    .operacional {
        margin: 0;
        border-bottom: none;
        text-indent: 0px;
    }
}

.operacional:hover {
    text-decoration: underline;
    color: skyblue;
}


.logo{
    display: flex;
    max-width: 400px;
    justify-content: flex-start;
    align-items: center;
    grid-gap:10px;
    gap:10px;
    margin-left: 10px;
}

.cabecalho_titulo {
    color: #c0c0c0;
    font-size: 24px;
}

@media (min-width: 1280px) {
    .cabecalho_titulo {
        font-size: 30px;
    }
}


.menu_toggle {
    background-color: black;
    height: 40px;
    width: 40px;

    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;

    order: -1;
}

@media (min-width: 700px){ 
    .menu_toggle {
        display: none;
    }
}

.corpo_toggle {
    background-color: skyblue;
    height: 35px;
    width: 35px;
    overflow: hidden;
    border-radius: 50%;

    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    cursor: pointer;
}

.detalhe_toggle {
    width: 15px;
    height: 3px;
    background-color: #c0c0c0;
    border: solid 2px black;
    border-radius: 5px;
    cursor: pointer;
}



*{  
    font-family: 'Open Sans', sans-serif;
    padding: 0;
    margin: 0;
    font-size: 12px;
    color: #c0c0c0;
}

html {
    scroll-behavior: smooth;
}

@media (min-width: 600px) { * {
    font-size: 16px
    }   
}

@media (min-width: 1280px) { * {
    font-size: 20px
    }   
}

html {
    background-color: black;
}

#root {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    grid-gap: 30px;
    gap: 30px;
    background: linear-gradient(to right, #14213D, #000000); 
}

.separacao {
    height: 50px;
}
::-webkit-scrollbar {
    width: 7px;
    background: black;
    /* mexe na scroll como um todo*/
}

::-webkit-scrollbar-thumb {
    background: skyblue;
    border-radius: 15px;
    /* mexe com a parte movel da scroll*/
}


