.sobre {
    display: flex;
    flex-direction: column;
    gap: 5px;
    align-items: flex-start;
    margin:0 10px;
}

@media (min-width: 1280px) { 
    .sobre {
        width: 100%;
        max-width: 1280px;
        display: flex;
        flex-direction: column;
        gap: 5px;
        align-items: flex-start;
        justify-content: center;
    }
}

.sobre_titulo {
    margin-left: 30px;
    font-size: 20px;
    color: #c0c0c0;
}

@media (min-width: 650px) { 
    .sobre_titulo {
        margin-left: 80px;
        font-size: 30px;
    }
}

.sobre_eu {
    text-indent: 15px;
}

