#menu_operacional{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    
    height: 0px;
    overflow: hidden;
    visibility: hidden;

    background-color: rgba(0, 0, 0, 0.7);

    width: 100%;
    
    transition: 1s;

    z-index: 1000000;
}

#menu .menu_operacional_open {
    height: 195px;
    visibility: visible;
    position: fixed;
    padding: 20px 0;
    top: 70px;
    left: 0;
}

@media (min-width: 600px) {
    #menu .menu_operacional_open {
        height: 215px;
    }
}

@media(min-width: 700px) {
    #menu_operacional {
        max-width: 550px;
        flex-direction: row;
        justify-content: center;
        align-items: center;

        transition: 0s;
        
        gap: 15px;
       
        height: 70px;
        background-color: transparent;
        visibility: visible;
    }
}

@media(min-width: 1280px) { 
    #menu_operacional{
        max-width: 880px;
        gap: 30px;
    }
}

.operacional {
    text-decoration: none;
    color: #c0c0c0;
    margin: 20px 0;
    border-bottom: solid 2px #c0c0c0;
    text-indent: 20px;
}

@media (min-width: 700px) {
    .operacional {
        margin: 0;
        border-bottom: none;
        text-indent: 0px;
    }
}

.operacional:hover {
    text-decoration: underline;
    color: skyblue;
}

