.logo{
    display: flex;
    max-width: 400px;
    justify-content: flex-start;
    align-items: center;
    gap:10px;
    margin-left: 10px;
}

.cabecalho_titulo {
    color: #c0c0c0;
    font-size: 24px;
}

@media (min-width: 1280px) {
    .cabecalho_titulo {
        font-size: 30px;
    }
}


.menu_toggle {
    background-color: black;
    height: 40px;
    width: 40px;

    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;

    order: -1;
}

@media (min-width: 700px){ 
    .menu_toggle {
        display: none;
    }
}

.corpo_toggle {
    background-color: skyblue;
    height: 35px;
    width: 35px;
    overflow: hidden;
    border-radius: 50%;

    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    cursor: pointer;
}

.detalhe_toggle {
    width: 15px;
    height: 3px;
    background-color: #c0c0c0;
    border: solid 2px black;
    border-radius: 5px;
    cursor: pointer;
}


