header {
    width: 100%;
    height: 50px;
    background: linear-gradient(to right, #14213D, #000000);

    padding: 15px 0;

    display: flex;
    justify-content: flex-start;
    align-items: center;

    position: fixed;
    top: 0;    
}

@media(min-width: 800px) { 
    header {
        justify-content: center;
    }
}

.menu_principal {
    width: 100%;
    height: 50px;
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    align-items: flex-start;

    gap: 20px;
}

@media (min-width: 700px) {
    .menu_principal {
        max-width: 1280px;
        flex-direction: row;
        justify-content: center;
        align-items: center;
    }
}

